import * as React from "react";
import { graphql, type HeadFC, type PageProps } from "gatsby";
import Layout from "./layout";
import { UserProfile } from "../interfaces";
import data from "../data/data.json";
import { useTranslation } from "gatsby-plugin-react-i18next";
import images from "../images/index"
import Carousel from "../components/carousel";
import ServicesCard from "../components/servicesCard";
import Values from "../components/valueTiles";
import ContactForm from "../components/contactForm";


const IndexPage: React.FC<PageProps> = () => {
  const userData: UserProfile = data; 
  const { t } = useTranslation();

  const carouselImages = [
    { src: images.office.src, alt: images.office.alt },
    { src: images.people_cleaning.src, alt: images.people_cleaning.alt },
    { src: images.woman_cleaning.src, alt: images.woman_cleaning.alt },
  ];
  

  const textOverlay = [
    t('carousel.text_1'),
    t('carousel.text_2'),
    t('carousel.text_3'),
  ];


  return (
    <main className={styles.container}>
      <Layout data={userData}>
        <Carousel images={carouselImages} textOverlay={textOverlay} speed={3000} />
        <div id={data?.menu[0]}>
          <ServicesCard />
        </div>
        <section id={data?.menu[1]} className={styles.whoContainer}>
          <img className={styles.whoImage} src={images.cleaning.src} alt={images.cleaning.alt} />
          <h2 className={styles.whoTitle}>{t("who.title")}</h2>
          <p className={styles.whoCopy}>{t("who.copy_1")}</p>
          <p className={styles.whoCopy}>{t("who.copy_2")}</p>
        </section>
        <section id={data?.menu[2]} className={styles.aboutUsContainer}>
          <img className={styles.aboutUsImage} src={images.cleaning_supplies.src} alt={images.cleaning_supplies.alt} />
          <div className="flex">
            <div>
              <h2 className={styles.aboutUsTitle}>{t("aboutUs.title")}</h2>
              <p className={styles.aboutUsCopy}>{t("aboutUs.copy")}</p>

            </div>
            <img className={styles.aboutUsPic} src={images.public_toilet.src} alt={images.public_toilet.alt} />
          </div>

        </section>

        <section id={data?.menu[3]} className="bg-orange/50 rounded-xl my-8 p-4 shadow">
        <h2 className={`${styles.whoTitle} text-center my-8`}>{t("menu.ourValues")}</h2>
          <Values />
        </section>

        <section id={data?.menu[4]} className={`${styles.aboutUsContainer} flex`}>
          <img src={images.mission.src} alt={images.mission.alt} />
          <div>
            <h2 className={`${styles.aboutUsTitle} px-4`}>{t('mission.title')}</h2>
            <p className={`${styles.aboutUsCopy} px-4`}>{t('mission.copy')}</p>
          </div>
        </section>

        <section id={data?.menu[5]} className="bg-blueLight/50 rounded-xl my-8 p-4 shadow">
          <ContactForm />
        </section>

      </Layout>
    </main>
  );
}

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;

const styles = {
  container: "bg-lightGray",
  whoContainer: "relative bg-orange/50 rounded-xl p-16 pb-12 mt-8 shadow",
  whoImage: "absolute w-72 opacity-25 right-4 bottom-4",
  whoTitle: "relative text-2xl text-white",
  whoCopy: "relative z-50 text-md py-4 text-white",
  aboutUsContainer: "relative bg-white rounded-xl p-16 pb-12 mt-8 shadow",
  aboutUsImage: "absolute w-60 opacity-25 left-4 bottom-4",
  aboutUsTitle: "relative text-2xl text-darkGray",
  aboutUsCopy: "relative z-50 text-md py-4 text-darkGray",
  aboutUsPic : ""
}