import React, { useState, useEffect, useTransition } from "react";
import {
  collection,
  addDoc,
  serverTimestamp,
  Firestore,
} from "firebase/firestore";
import ReCAPTCHA from "react-google-recaptcha";
import emailjs from "emailjs-com";
import { app } from "../../firebaseConfig";
import { Box, Button, TextField } from "@mui/material";
import { useTranslation } from "gatsby-plugin-react-i18next";
import QuestionAnswerTwoToneIcon from '@mui/icons-material/QuestionAnswerTwoTone';
import MapTwoToneIcon from '@mui/icons-material/MapTwoTone';
import PhoneInTalkTwoToneIcon from '@mui/icons-material/PhoneInTalkTwoTone';


let db: Firestore | null = null;

if (typeof window !== "undefined") {
  const { getFirestore } = require("firebase/firestore");
  db = getFirestore(app);
}

// Definimos el tipo de `ErrorState` para evitar el error
type ErrorState = {
  email: string;
  phone: string;
  general: string;
};

const ContactForm: React.FC = () => {
  const { t } = useTranslation();
  const [formData, setFormData] = useState({
    fullName: "",
    email: "",
    phone: "",
    message: "",
  });

  const [error, setError] = useState<ErrorState>({
    email: "",
    phone: "",
    general: "",
  });

  const [captchaToken, setCaptchaToken] = useState("");
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    // Habilita el renderizado del lado del cliente
    setIsClient(typeof window !== "undefined");
  }, []);

  // Validar campos de correo y teléfono
  const validateEmail = (email: string) =>
    /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/.test(email);
  const validatePhone = (phone: string) => /^\+?\d{10,15}$/.test(phone);

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
    setError((prevError) => ({ ...prevError, [name]: "" }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Validación de campos
    let isValid = true;
    const newError: ErrorState = { email: "", phone: "", general: "" };

    if (!validateEmail(formData.email)) {
      newError.email = t("form.emailError");
      isValid = false;
    }
    if (!validatePhone(formData.phone)) {
      newError.phone = t("form.phoneError");
      isValid = false;
    }
    if (!captchaToken) {
      newError.general = t("form.captchaError");
      isValid = false;
    }

    if (!isValid) {
      setError(newError);
      return;
    }

    setError({ email: "", phone: "", general: "" });

    try {
      // Guardar en Firestore solo si `db` está disponible
      if (db) {
        await addDoc(collection(db, "contactForm"), {
          ...formData,
          timestamp: serverTimestamp(),
        });
      }

      // Enviar correo usando EmailJS
      await emailjs.send(
        "service_sm8nf4i",
        "template_jyqdnxg",
        {
          to_name: "juanpablo@riano.ca",
          from_name: formData.fullName,
          message: formData.message,
          reply_to: formData.email,
        },
        "u2Op-9wm_t5yR4it5"
      );

      setFormData({ fullName: "", email: "", phone: "", message: "" });
      setCaptchaToken("");
    } catch (error) {
      setError((prevError) => ({
        ...prevError,
        general:
          "Hubo un error al enviar el formulario. Inténtalo nuevamente más tarde.",
      }));
      console.error("Error al enviar el formulario:", error);
    }
  };

  // reCAPTCHA
  const handleReCaptcha = (token: string | null) => {
    if (token) setCaptchaToken(token);
  };

  return (
    <div className={styles.container}>
      <div className={styles.panel}>
        <div className={styles.chip}>
          <QuestionAnswerTwoToneIcon className={styles.icon} />
          <div>
            <h3 className="font-bold text-xl">
                {t("form.chatTitle")}
              </h3>
            <p className="w-full mt-2">{t("form.chatCopy")}</p>
            <p className="w-full mt-2">{t("form.chat")}</p>
          </div>
        </div>
        <div className={styles.chip}>
          <MapTwoToneIcon className={styles.icon} />
          <div>
            <h3 className="font-bold text-xl">
              {t("form.visitTitle")}
            </h3>
            <p className="w-full mt-2">{t("form.visitCopy")}</p>
            <p className="w-full">{t("form.visit_1")}</p>
            <p className="w-full">{t("form.visit_2")}</p>
            <p className="w-full">{t("form.visit_3")}</p>
            <div className="w-full mt-2">
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2799.760265491016!2d-73.49265568749829!3d45.43433377095296!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x4cc90fa8e63c9c03%3A0xd81f311632cbabf6!2s9270%20Rue%20Riverin%2C%20Brossard%2C%20QC%20J4X%203A4!5e0!3m2!1ses!2sca!4v1731612746668!5m2!1ses!2sca"  loading="lazy"></iframe>
            </div>
          </div>
        </div>
        <div className={styles.chip}>
          <PhoneInTalkTwoToneIcon className={styles.icon} />
          <div>
            <h3 className="font-bold text-xl">
              {t("form.callTitle")}
            </h3>
            <p className="w-full my-2">{t("form.callCopy")}</p>
            <a href="tel:+15149381119" className="w-full">{t("form.call")}</a>
          </div>
        </div>
      </div>
      {/* <form onSubmit={handleSubmit} className="max-w-lg mx-auto p-4">
        {isClient && (
          <ReCAPTCHA
            sitekey="6Lce2X0qAAAAAGvyv_z_wN8ZvlQB1gvi8NaAcjd7"
            onChange={handleReCaptcha}
          />
        )}
        <div>
          <label>Nombre Completo</label>
          <input
            type="text"
            name="fullName"
            value={formData.fullName}
            onChange={handleInputChange}
            className="w-full p-2 border rounded"
            required
          />
        </div>
        <div>
          <label>Correo Electrónico</label>
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleInputChange}
            className="w-full p-2 border rounded"
            required
          />
          {error.email && <p className={styles.errorMessage}>{error.email}</p>}
        </div>
        <div>
          <label>Número de Teléfono</label>
          <input
            type="tel"
            name="phone"
            value={formData.phone}
            onChange={handleInputChange}
            required
            className="w-full p-2 border rounded"
          />
          {error.phone && <p className={styles.errorMessage}>{error.phone}</p>}
        </div>
        <div>
          <label>Mensaje</label>
          <textarea
            name="message"
            value={formData.message}
            onChange={handleInputChange}
            rows={4}
            className="w-full p-2 border rounded"
            required
          />
        </div>
        {error.general && <p className={styles.errorMessage}>{error.general}</p>}
        <button
          type="submit"
          className="w-full p-2 bg-orange text-white rounded mt-2"
          disabled={!captchaToken}
        >
          Enviar
        </button>
      </form> */}

      <Box
        component="form"
        // sx={{ "& > :not(style)": { mt: 6, width: "100%" } }}
        noValidate
        autoComplete="off"
        onSubmit={handleSubmit}
        className={styles.form}
      >
        {error.general && <p className={styles.errorMessage}>{error.general}</p>}
        <TextField
          id="name"
          label={t("form.name")}
          variant="standard"
          sx={{
            "& .MuiInput-underline:before": { borderBottomColor: "#1e58a8" },
            "& .MuiInput-underline:after": { borderBottomColor: "#f05423" },
            "& .MuiInputBase-input": { color: "#333333" },
            "& .MuiFormLabel-root": { color: "#1e58a8" },
            "& .MuiFormLabel-root.Mui-focused": { color: "#f05423" },
          }}
          name="fullName"
          value={formData.fullName}
          onChange={handleInputChange}
          required
          className={styles.input}
        />
        <TextField
          id="email"
          label={t("form.email")}
          variant="standard"
          sx={{
            "& .MuiInput-underline:before": { borderBottomColor: "#1e58a8" },
            "& .MuiInput-underline:after": { borderBottomColor: "#f05423" },
            "& .MuiInputBase-input": { color: "#333333" },
            "& .MuiFormLabel-root": { color: "#1e58a8" },
            "& .MuiFormLabel-root.Mui-focused": { color: "#f05423" },
          }}
          type="email"
          name="email"
          value={formData.email}
          onChange={handleInputChange}
          required
          className={styles.input}
        />
        {error.email && <p className={styles.errorMessage}>{error.email}</p>}
        <TextField
          id="phone"
          label={t("form.phone")}
          variant="standard"
          sx={{
            "& .MuiInput-underline:before": { borderBottomColor: "#1e58a8" },
            "& .MuiInput-underline:after": { borderBottomColor: "#f05423" },
            "& .MuiInputBase-input": { color: "#333333" },
            "& .MuiFormLabel-root": { color: "#1e58a8" },
            "& .MuiFormLabel-root.Mui-focused": { color: "#f05423" },
          }}
          type="tel"
          name="phone"
          value={formData.phone}
          onChange={handleInputChange}
          required
          className={styles.input}
        />
        {error.phone && <p className={styles.errorMessage}>{error.phone}</p>}
        <TextField
          id="massage"
          label={t("form.message")}
          variant="standard"
          sx={{
            "& .MuiInput-underline:before": { borderBottomColor: "#1e58a8" },
            "& .MuiInput-underline:after": { borderBottomColor: "#f05423" },
            "& .MuiInputBase-input": { color: "#333333" },
            "& .MuiFormLabel-root": { color: "#1e58a8" },
            "& .MuiFormLabel-root.Mui-focused": { color: "#f05423" },
          }}
          name="message"
          value={formData.message}
          onChange={handleInputChange}
          rows={4}
          multiline
          required
          className={styles.input}
        />
        {isClient && (
          <ReCAPTCHA
            sitekey="6Lce2X0qAAAAAGvyv_z_wN8ZvlQB1gvi8NaAcjd7"
            onChange={handleReCaptcha}
            className="flex justify-end my-2"
          />
        )}
        <Button
          type="submit"
          disabled={!captchaToken}
          variant="contained"
          className="capitalize"
        >{t("form.button")}</Button>
      </Box>
    </div>
  );
};

export default ContactForm;

const styles = {
  container: "flex text-darkGray/70",
  panel: "w-full sm:w-2/5 p-12",
  chip: "flex mt-2",
  icon: "text-orange mr-4",
  form: "flex flex-col w-full sm:w-3/5 px-12",
  input: "mt-8",
  errorMessage: "text-red-600 text-xs"
}